.book-info {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    text-align: center;
    width: 100%;
    gap: 20px;
    margin-top: 20px;
    margin-bottom: 60px;
}

.book-info img{
  width: 200px;
  height: auto;
}

.book-info p{
    font-size: 18px;
    line-height: 30px;
    text-align: justify;
    background-color: rgba(243, 176, 137, 0.5);
    padding: 20px;
    width: 1000px;
    backdrop-filter: blur( 12px );
    -webkit-backdrop-filter: blur( 10px );
    font-weight: 450;
    color: rgb(13, 13, 13);
}

.book-info h2 {
    font-style: italic;
}

.book-info button {
    padding: 16px;
    font-size: 18px;
    border: none;
    color: white;
}

.buttons {
    display: flex;
    justify-content: center;
    text-align: center;
    align-items: center;
    gap: 10px;
}

.insta {
    background-color: #c0007a;
}

.amazon {
    background-color: #ff5341;
}

.books {
    margin-bottom: 10px;
    width: 100%;
}

.info {
    display: flex;
    flex-direction: column;
    line-height: 1px;
}

.resenha {
    margin-top: 10px;
}

@media only screen and (max-width: 768px) {
    body {
        width: 100%;
    }
    .book-info {
        display: flex;
        flex-direction: column;
    }
    .book-info img {
        width: 100px;
    }
    .book-info button{
        font-size: 15px;
        padding: 8px;
        margin-top: 5px;
    }
    .book-info p{
        font-size: 11px;
        line-height: 13px;
        width: 80%;
        margin-left: 30px;
    }
    .book-info h1{
        font-size: 20px;
    }
    .book-info h2{
        font-size: 18px;
    }
    .info {
        font-size: 11px;
        color: white;
    }
}
