.search {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 60px;
    gap: 20px;
}

.search-general {
  margin-bottom: 10px;
  width: 100%;
  margin-left: 40px;
}

.search label {
   gap: 10px;
   font-size: x-large;
   font-weight: bolder;
}

.search input{
    padding: 18px;
    margin-left: 1%;
    margin-top: 5%;
}

.book-search {
    display: flex;
    justify-content: space-around;
    align-items: center;
    text-align: center;
    width: 100%;
    gap: 30px;
}

.book-search h2 {
  font-style: italic;
}

.book-search img {
  width: 200px;
  height: auto;
}

.book-search p{
    font-size: 18px;
    line-height: 30px;
    text-align: justify;
    background-color: rgba(214, 145, 105, 0.5);
    padding: 20px;
    width: 1000px;
    backdrop-filter: blur( 4px );
    -webkit-backdrop-filter: blur( 10px );
    color: rgb(13, 13, 13);
    font-weight: 450;
    margin-right: 60px;
}

.book-search button {
    padding: 16px;
    font-size: 18px;
    border: none;
    color: white;
}

.buttons-search {
  display: flex;
    justify-content: center;
    text-align: center;
    align-items: center;
    gap: 10px;
}

@media only screen and (max-width: 768px) {
  body {
    width: 100%;
}
  .book-search {
      display: flex;
      flex-direction: column;
      gap: 10px;
  }
  .book-search img {
    width: 100px;
  }
  .book-search button{
    font-size: 15px;
    padding: 8px;
    margin-top: 5px;
  }
  .book-search p{
    font-size: 11px;
    line-height: 13px;
    width: 90%;
  }
  .book-search h1{
    font-size: 20px;
  }
  .book-search h2{
    font-size: 18px;
  }
  .details {
    font-size: 11px;
    color: white;
  }
}