.about {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-left: 150px;
    margin-right: 150px;
}

.imginsta {
    width: 400px;
    margin-top: 50px;
}

.imgbooks {
    width: 300px;
    border-radius: 50%;
}

.about p {
    width: 35%;
    font-size: 20px;
    text-align: justify;
    line-height: 40px;
    backdrop-filter: blur( 8px );
    -webkit-backdrop-filter: blur( 10px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
    padding: 20px;
    font-weight: bold;
}

@media only screen and (max-width: 768px) {
    .about {
        display: flex;
        flex-direction: column;
    }
    .imgbooks {
        width: 150px;
        margin-top: 10px;
    }
    .about p{
        width: 150%;
        color: white;
        font-size: 12px;
        line-height: 20px;
        background-color: rgb(15, 15, 15);
    }
    .imginsta {
        width: 250px;
        margin-top: 5px;
    }
}